}

.slide-frame:before, .slide-frame:after {
  content: "";
  height: 100%;
  width: 8%;
  z-index: 2;
  background: linear-gradient(to right, #0088cc39 0%, #fff0 100%);
  position: absolute;
}

.slide-frame:before {
  top: 0;
  left: 0;
}

.slide-frame:after {
  top: 0;
  right: 0;
  transform: rotateZ(180deg);
}

